}
.services__title {
  background-color: #002169;
}
.services__title p {
  color: #fff;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.25;
  margin: 0.5rem 0;
}

.services__nav--first {
  background-color: #F5F5F5;
}
.services__nav--first .col-12 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

@media (max-width: 991.98px) {
  .services__nav--first h1, .services__nav--first .h1 {
    font-size: 1.25rem;
  }
}
@media (min-width: 992px) {
  .services__title,
.services__navigation {
    display: none;
  }
}
.services__img {
  margin-bottom: 5rem;
}
.services__img img {
  aspect-ratio: 1/1;
  width: 100%;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
}

@media (max-width: 991.98px) {
  .services__img img {
    aspect-ratio: 3/2;
  }
}
ul.services__list {
  margin-bottom: 2rem;
}
ul.services__list li {
  margin-bottom: 1rem;
}
ul.services__list li:last-child {
  margin-bottom: 0;
}

.services__nav--last {
  background-color: #F5F5F5;
}
.services__nav--last .nav {
  width: 100%;
  margin: 1rem 0;
  display: flex;
  justify-content: space-between;
}
.services__nav--last .nav .nav-link {
  color: #808080;
  padding: 1rem 0;
}
.services__nav--last .nav .nav-link.active {
  color: #0075C9;
  position: relative;
}
.services__nav--last .nav .nav-link.active::after {
  content: "";
  width: 100%;
  height: 0.25rem;
  background-color: #002169;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

